  height: 100vh;
  margin: 0;
  padding: 0;
}

.page-wrapper {
  min-height: 100vh;
  position: relative;
  overflow: hidden;
}

.page-wrapper:before {
  content: "";
  z-index: -3;
  background: linear-gradient(#000810 0%, #010509 100%);
