.header-lang {
  color: #fff;
  letter-spacing: -.48px;
  background: linear-gradient(to left, #0072f880, #0072f838);
  border-radius: 36px;
  width: 93px;
  height: 32px;
  margin-left: 32px;
  font-size: 16px;
  font-weight: 700;
  line-height: 100%;
  text-decoration: none;
  position: relative;
}

.header-lang:before {
  content: "";
  background: #000810;
  border-radius: 34px;
  position: absolute;
  inset: 1px;
}

.header-lang:after {
  content: "";
  opacity: 0;
  z-index: 1;
  background: #0072f8;
  border-radius: 36px;
  transition: all .4s;
  position: absolute;
  inset: 0;
}

.header-lang:hover:after {
  opacity: 1;
}

.header-lang svg {
  fill: #fff;
  width: 16px;
  height: 16px;
  margin-right: 10px;
}

.header-burger {
